






import { Vue, Component } from 'vue-property-decorator';
import axios from 'axios';
import wx from 'weixin-js-sdk';

@Component({})
export default class App extends Vue {
  private created() {
    this.setRem();
    this.weixinInit();
  }
  private setRem() {
    const windowWidth: number = document.documentElement.clientWidth;
    const rem: number = windowWidth / 7.5;
    document.documentElement.style.fontSize = rem + 'px';
  }
  private weixinInit() {
    axios({
      method: 'POST',
      url: 'http://web-shen.club/wxJssdk/getJssdk',
      data: {
        url: window.location.href.split('#')[0],
      },
    }).then((res) => {
      const config = res.data.data;
      wx.config({
        debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        appId: config.appId, // 必填，公众号的唯一标识
        timestamp: config.timestamp, // 必填，生成签名的时间戳
        nonceStr: config.nonceStr, // 必填，生成签名的随机串
        signature: config.signature, // 必填，签名
        jsApiList: [
          'onMenuShareTimeline',
          'onMenuShareAppMessage',
          'checkJsApi',
        ], // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
      });
    });

    wx.ready(() => {
      // 需在用户可能点击分享按钮前就先调用
      const shareData = {
        title: '万万想不到：你能撑到60秒么？', // 分享标题
        desc: '不努力一把，你都不知道什么是绝望', // 分享描述
        link: location.href, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: 'http://web-shen.club/images/share.png', // 分享图标
        success() {
          // 设置成功
          console.log(111);
        },
      };

      wx.onMenuShareAppMessage(shareData);
      wx.onMenuShareTimeline(shareData);
    });
  }
}
